<template>
  <div>
    <h2 class="text-center mt-5">الطلاب</h2>
    <hr />
    <div class="row align-items-center p-2 justify-content-center _inputs1" dir="rtl">
      <div class="col-md-3">
        <div class="form-group">
          <label style="font-size: 0.7rem" for=""
            >ابحث بالأسم أو التليفون أو العنوان أو المرحلة الدراسية</label
          >
          <input
            v-model="filter.query"
            class="form-control"
            type="text"
            placeholder="search"
          />
        </div>
      </div>
      <div class="col-md-3">
        <div class="form-group">
          <label for="">اختار المستوى</label>
          <select class="form-control" v-model="filter.stage">
            <option
              v-for="stage in $store.getters.stages"
              :key="stage.value"
              :value="stage.value"
            >
              {{ stage.text }}
            </option>
          </select>
        </div>
      </div>
      <div class="col-md-2">
        <div class="form-group">
          <label for="">مساعدين أم طلاب</label>
          <select v-model="filter.role" class="form-control">
            <option value="0">طلاب</option>
            <option value="1">مساعدين</option>
          </select>
        </div>
      </div>

      <div class="col-md-3">
        <div class="form-group">
          <label for="">الوحدة</label>
          <select v-model="filter.units" class="form-control">
            <option value="">الجميع</option>
            <option v-for="unit in units" :key="unit.number" :value="unit.number">
              {{ unit.name }}
            </option>
          </select>
        </div>
      </div>

      <div class="col-md-3">
        <div class="form-group">
          <label for="">الطلبة المسجلين</label>
          <select v-model="filter.registered" class="form-control">
            <option value="">الجميع</option>
            <option :value="false">مسموح له بالتسجيل</option>
            <option :value="true">غير مسموح له بالتسجيل مرة اخرى</option>
          </select>
        </div>
      </div>
      <!-- <div class="col-md-3">
        <button @click="unconfirmAll" class="btn btn-danger">Unconfirm all</button>
      </div> -->
      <div class="col-md-3 center">
        <button @click="filter = { ...filter }" class="btn btn-primary w-100">
          Find
        </button>
      </div>
    </div>
    <hr />
    <div v-if="loading" class="center">
      <loading />
    </div>
    <div class="row justify-content-center" dir="rtl">
      <div class="col-md-3 center">
        <div class="form-group">
          <label>فتح أعادة التسجيل لكل الطلبة</label>
          <button @click="allowAll" class="btn btn-success">Allow all</button>
        </div>
      </div>
    </div>
    <!----------------------------------------------------------------------------->
    <!----------------------------------------------------------------------------->
    <!----------------------------------------------------------------------------->
    <div>
      <server-responsive-table
        title="Students"
        :api="'/getUsers'"
        itemsName="users"
        :returnedItems="users"
        :colsTitles="[
          'username',
          'email',
          'phone',
          'parent phone',
          'code / address',
          'allowed units',
        ]"
        :cols="['fullname', 'email', 'phone', 'parentPhone', 'address', 'units']"
        :colsTypes="[
          'readonly',
          'readonly',
          'readonly',
          'readonly',
          'readonly',
          'readonly',
        ]"
        :btns="[
          {
            icon: 'eye',
            method: (item) => $router.push('/dashboard/studentFile/' + item._id),
          },
          { icon: 'trash', method: (item) => remove(item._id) },
          { icon: 'user', method: (item) => toggleRole(item._id) },
          { icon: 'pencil', method: (item) => $router.push('/update/' + item._id) },
          {
            icon: 'فتح التسجيل',
            type: 'text',
            method: (item) => allowRegister(item._id),
            setBtnStyle: setBtnStyle,
          },
          {
            icon: 'Allowed units',
            type: 'text',
            method: (item) => {
              allowedUnits = item.units;
              choosenUser = item;
              dialogOpen = true;
            },
          },
        ]"
        color="#555"
        :filter="filter"
        @itemsFetched="(items) => (filteredUsers = items)"
        :setRowStyle="() => {}"
      />
    </div>
    <!----------------------------------------------------------------------------->
    <!----------------------------------------------------------------------------->
    <!----------------------------------------------------------------------------->

    <div style="opacity: 0">
      <!-- <v-container fluid> -->
      <v-dialog transition="dialog-top-transition" v-model="dialogOpen">
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            ref="dialog"
            style="font-size: 0.67rem"
            color="primary"
            v-bind="attrs"
            v-on="on"
            >allowed units</v-btn
          >
        </template>
        <template v-slot:default="dialog" class="py-4">
          <v-card>
            <v-toolbar color="primary" dark>Choose user allowed units</v-toolbar>
            <v-card-text style="height: 300px">
              <v-checkbox
                style="width: fit-content"
                v-model="allowedUnits"
                v-for="unit in getStageUnits(choosenUser._id)"
                :key="unit.number"
                :label="unit.name"
                :value="unit.number"
              ></v-checkbox>
            </v-card-text>
            <v-card-actions class="justify">
              <v-row>
                <v-col>
                  <v-btn
                    :loading="unitsLoading"
                    :disabled="unitsLoading"
                    text
                    color="success"
                    @click="updateAllowedUnits(choosenUser._id)"
                    >update</v-btn
                  >
                </v-col>
                <v-col>
                  <v-btn text @click="dialog.value = false" color="error">Close</v-btn>
                </v-col>
              </v-row>
            </v-card-actions>
          </v-card>
        </template>
      </v-dialog>
      <!-- </v-container> -->
    </div>

    <app-iframe :src="$store.getters.expVideos.users" />
    <!--------------------------------------------------------->
    <!--------------------------------------------------------->
  </div>
</template>

<script>
import axios from "axios";
import { parse } from "querystring";
import { getStage } from "../../functions";
export default {
  created() {},
  data() {
    return {
      users: [],
      loading: false,
      unitsLoading: false,
      filteredUsers: [],
      dialogOpen: false,
      choosenUser: null,
      allowedUnits: [],
      stages: {},
      type: "",
      stage: "all",
      unit: "",
      ////////////////////
      filter: {
        stage: "6",
        units: "",
        role: 0,
        registered: false,
        query: "",
      },
    };
  },
  computed: {
    units() {
      return this.$store.getters.sections.filter((sec) => sec.stage == this.filter.stage);
    },
  },
  methods: {
    remove(id) {
      const confirmed = confirm("مسح الطالب نهائيا؟");
      if (!confirmed) return;
      axios.get("/deleteUser/" + id).then((res) => {
        const index = this.filteredUsers.findIndex((fu) => {
          return fu._id === id;
        });
        this.filteredUsers.splice(index, 1);
        this.user = { ...this.filteredUsers };
      });
    },
    ////////////////////////////

    toggleRole(id) {
      axios.get("/toggleUserRole/" + id).then((res) => {
        if (res.data.err) return;
        this.$store.dispatch("writemessage", "تم تعديل صلاحيات المستخدم");
        const index = this.filteredUsers.findIndex((fu) => {
          return fu._id === id;
        });
        this.filteredUsers.splice(index, 1);
        this.user = { ...this.filteredUsers };
      });
    },

    updateAllowedUnits(id) {
      this.unitsLoading = true;
      axios
        .post("/updateAllowedUnits/" + id, { units: this.allowedUnits })
        .then((res) => {
          this.unitsLoading = false;
          if (res.data.done) {
            this.$store.dispatch("writemessage", "تم تحديث الوحدات المتاحة للطالب");
            const user = res.data.user;
            const index = this.filteredUsers.findIndex((fu) => {
              return fu._id === user._id;
            });
            this.filteredUsers[index] = user;
            this.users = [...this.filteredUsers];
          } else {
            this.$store.dispatch(
              "writemessage",
              "حدثت مشكلة اثناء تعديل الوحدات المتاحة للطالب"
            );
          }
        });
    },

    search() {},

    allowRegister(id) {
      axios.get("/allowRegister/" + id).then((res) => {
        if (res.data.done) {
          this.$store.dispatch("writemessage", "تم تفعيل التسجيل للطالب");
          const index = this.filteredUsers.findIndex((fu) => {
            return fu._id === id;
          });
          this.filteredUsers[index].registered = false;
          this.users = [...this.filteredUsers];
        }
      });
    },

    unconfirmAll() {
      const confirmed = confirm("هل انتا متاكد من اتمام هذه العملية؟");
      // if (!confirmed) return;
      axios.get("/unconfirmAll").then((res) => {
        if (res.data.done) {
          this.users = this.users.map((user) => {
            return { ...user, confirmed: 0 };
          });
          this.$store.dispatch("writemessage", "تبع الغاء تفعيل كل الطلبة");
        } else {
          this.$store.dispatch("writemessage", "حاول مرة أخرى");
        }
      });
    },

    allowAll() {
      const confirmed = confirm("هل انتا متاكد من اتمام هذه العملية؟");
      // if (!confirmed) return;
      axios.get("/allowAll").then((res) => {
        if (res.data.done) {
          this.users = this.filteredUsers.map((user) => {
            return { ...user, registered: false };
          });
          this.$store.dispatch("writemessage", "تم فتح التسجيل لكل الطلبة");
        } else {
          this.$store.dispatch("writemessage", "حاول مرة أخرى");
        }
      });
    },
    ///////////////////
    generateStudents() {
      this.loading = true;
      axios.get("/generateUsers").then((res) => {
        this.loading = false;
        if (res.data.err) return alert(res.data.err);
      });
    },

    getStageUnits(id) {
      const user = this.filteredUsers.find((user) => user._id == id);
      const stage = user.stage;
      const units = this.$store.getters.sections.filter((sec) => sec.stage == stage);
      return units;
    },
    setBtnStyle(item) {
      console.log(item);
      if (!item.registered) return "display: none";
    },
  },
};
</script>

